/**
 * # 
 * `obj1``obj2`
 * ## Example
 * 
 *
 * `options``buttons``obj1``buttons``this.options``buttons`
 * ```ts
 * this.options = deepMixins(
      options,
      {
        buttons: [],
      }
    );
 * ```
 * @param obj1 
 * @param obj2 
 * @returns
 */
export function deepMixins(obj2, obj1) {
    let key;
    for (key in obj2) {
        obj1[key] =
            obj1[key] &&
                obj1[key].toString() === "[object Object]" &&
                obj2[key] &&
                obj2[key].toString() === "[object Object]"
                ? deepMixins(obj1[key], obj2[key])
                : obj2[key] === undefined
                    ? (obj2[key] = obj1[key])
                    : (obj1[key] = obj2[key]);
    }
    return obj1;
}
/**
 *  [requestAnimationFrame](https://developer.mozilla.org/zh-CN/docs/Web/API/window/requestAnimationFrame) 
 * @param callback
 * @param ms
 */
export function timeout(callback, ms = 0) {
    let start;
    function step(timestamp) {
        if (start === undefined)
            start = timestamp;
        const elapsed = timestamp - start;
        if (elapsed < ms) {
            window.requestAnimationFrame(step);
        }
        else {
            return callback(elapsed);
        }
    }
    window.requestAnimationFrame(step);
}
